@import '@/theme/variables.scss';

.form-subscription {
  position: relative;

  & &__submit {
    position: absolute;
    bottom: 0;
    right: 0;
    width: calc_rem(44);
    height: calc_rem(44);
    border: 0;
    border-radius: 0;
    box-shadow: none;
    background-color: transparent;
    background-image: url(img/arrow_forward.svg);
    background-position: center;
    background-size: auto;
    background-repeat: no-repeat;
    cursor: pointer;
  }
}